import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import AOS from 'aos';
import Bio from '../sections/Bio';
import Intro from '../sections/Intro';
import Skills from '../sections/Skills';
import SectionBackground from '../sections/SectionBackground';
import Contact from '../sections/Contact';
import Layout from '../components/Layout';
import AnimatedArrow from '../components/AnimatedArrow';
import IndexBg from '../images/index-bg.png';
import 'aos/dist/aos.css';


const HomeBanner = styled.section`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  width: 100%;
  min-height: 200px;
  position: relative;
  height: 100vh;
  z-index: 1;

  :after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-image: radial-gradient(ellipse closest-side, rgba(15, 14, 22, 0.75), #100e17), url(${IndexBg});
    background-size: cover;
    background-repeat: no-repeat;
    opacity: 0.75;
  }
`;
const HomeBannerItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

const HomeBannerBio = styled(Bio)`
  margin:15px;
  padding:15px;
`;
const ScrollButton = styled(AnimatedArrow)`
  cursor:pointer;
`;

const ChildSection = styled.section`
  padding: 100px 0
`;

const scrollToRef = (ref) => window.scrollTo(0, ref.current.offsetTop);

const Index = (props) => {
  const myRef = useRef(null);
  const executeScroll = () => {
    scrollToRef(myRef);
  };

  useEffect(() => {
    AOS.init({
      duration: 2000,
    });
  });

  const { location } = props;
  return (
    <Layout location={location} theme="dark">
      <HomeBanner>
        <HomeBannerItem>
          <HomeBannerBio />
        </HomeBannerItem>
        <HomeBannerItem>
          <ScrollButton onClick={executeScroll} />
        </HomeBannerItem>
      </HomeBanner>

      <ChildSection ref={myRef} data-aos="fade-up">
        <div className="container">
          <Intro />
        </div>
      </ChildSection>


      <ChildSection data-aos="fade-up">
        <div className="container">
          <Skills />
        </div>
      </ChildSection>


      <ChildSection data-aos="fade-up">
        <div className="container">
          <Contact />
        </div>
      </ChildSection>

    </Layout>
  );
};
Index.propTypes = {
  location: PropTypes.shape({
    hash: PropTypes.string.isRequired,
    key: PropTypes.string.isRequired,
    pathname: PropTypes.string.isRequired,
    search: PropTypes.string.isRequired,
  }).isRequired,
};
export default Index;
